<template>
  <div class="feedback">
    <app-header
      title="帮助与反馈"
      @click-right="$router.push('/user/feedback/history')"
    >
      <div slot="right">历史</div>
    </app-header>
    <div class="blank"></div>
    <!-- <van-cell-group>
      <van-switch-cell
        v-model="shake"
        title="摇一摇反馈问题"
        active-value="on"
        inactive-value="off"
        @change="shakeSwitch"
      />
    </van-cell-group> -->
    <div class="blank"></div>
    <div class="text-wrapper">
      <text-box
        ref="textbox"
        :remark="model.remark"
        placeholder="描述您遇到的问题或建议"
        @input="updateRemark"
      ></text-box>
    </div>
    <section class="image-list">
      <div
        class="img-wrapper"
        :class="{ ml20: index > 0 }"
        v-for="(item, index) in model.photoList"
        :key="index"
      >
        <img :src="item.img_view" alt="photo" @click="showLargeImg(index)" />
        <i
          class="delete-btn super-app-iconfont super-app-icon-clear"
          @click="removeImg(index)"
        ></i>
      </div>

      <div
        v-show="!model.photoList || model.photoList.length < 3"
        class="append-btn"
        :class="{ ml20: model.photoList && model.photoList.length > 0 }"
        @click="addImage"
      >
        <i class="super-app-iconfont super-app-iconzhaopian"></i>
      </div>
    </section>
    <div class="footer">
      <app-button class="login-btn" @click="submit">提交</app-button>
    </div>
    <van-action-sheet
      v-model="showImageAction"
      :actions="actions"
      cancel-text="取消"
      @select="selectAction"
      @cancel="cancelSelect"
    />
  </div>
</template>
<script>
import appButton from '@components/app-button/index.vue';
import textBox from '@components/textbox';
import FeedbackModel from '@src/model/feedback-model.js';
import settingService from '@services/setting-service';
export default {
  components: {
    appButton,
    textBox,
  },
  data() {
    return {
      shake: 'on',
      remark: '',
      model: {},
      showImageAction: false,
      actions: [],
    };
  },
  activated() {
    this.model = FeedbackModel.model;
    this.actions = FeedbackModel.actions;
  },
  methods: {
    updateRemark(val) {
      this.model.remark = val;
    },
    shakeSwitch(check) {
      settingService.setShake(check);
    },
    addImage() {
      this.showImageAction = true;
    },

    removeImg(index) {
      FeedbackModel.removeImg(index);
    },

    showLargeImg(index) {
      FeedbackModel.showLargeImg(index);
    },

    selectAction(item) {
      return FeedbackModel.takePhoto(item.index)
        .then(() => {
          this.showImageAction = false;
        })
        .catch(err => {
          this.$toast(err || '获取照片失败');
        });
    },

    cancelSelect() {
      this.showImageAction = false;
    },
    submit() {
      FeedbackModel.submit()
        .then(msg => {
          this.$toast.clear();
          console.log('msg ===> ', msg || '提交成功');
          this.$toast(msg || '提交成功');
          this.model.remark = '';
          this.model.photoList = [];
          this.model.uploadLog = true;
          this.model.currType = 'optimumSuggest';
          // this.close();
        })
        .catch(error => {
          this.$toast.clear();
          this.$toast(error || '提交失败');
        });
    },
  },
};
</script>
