    background: rgba(211, 211, 211, 1);
    border-radius: 2px;

    img {
      width: 100%;
      height: 100%;
    }

    .delete-btn {
      position: absolute;
      font-size: 16px;
      color: #7e899f;
      // width: 20px;
      // height: 20px;
      top: -8px;
      right: -8px;
      // background: url('~@assets/images/shake/delete.png');
      background-size: contain;
    }
  }

  .img-wrapper:not(:first-child) {
    margin-left: 20px;
  }

  .append-btn {
    width: 60px;
    height: 60px;
    border-radius: 4px;
    border: 1px dashed #dde1e7;
    display: flex;
    align-items: center;
    justify-content: center;

    .super-app-iconfont {
      font-size: 24px;
    }
  }
}
