import localStorage from '@yl/localstorage';
import localStorageKeys from '@src/config/const';
/**
 * 设置摇一摇开关
 * @param {*} isShake
 */
const setShake = function(isShake) {
  localStorage.set(
    localStorageKeys.SHAKE_TO_FEED_BACK,
    isShake,
    localStorage.level.user
  );
};
/**
 * 获取摇一摇开关(默认开启)
 */
const getShake = function() {
  return localStorage.getString(
    localStorageKeys.SHAKE_TO_FEED_BACK,
    localStorage.level.user
  );
};
export default {
  setShake,
  getShake,
};
